	module.exports = {"data":{"post":{"html":"<p>(Yes, <a title=\"Lilli Marleen\" href=\"http://lillimarleen.blogspot.com/\">Lilli</a>, I'm talking to you!) If you want to make it easier for people to read your blog, please turn on your site feed! In your Blogger interface, go to Settings -> Site Feed, set Publish Site Feed to Yes and Descriptions to Full. Then those of us with RSS/Atom aggregators will be able to see your new posts right away. I even started a <a title=\"PapaSpot\" href=\"http://papaspot.blogspot.com/\">test blogspot blog</a> to <a href=\"http://papaspot.blogspot.com/atom.xml\">see how it works</a>. Thank you!</p>","fields":{"title":"Attention Blogspot Users","date":"30 Jan 2004","url":"https://papascott.de/2004-01-30-attention-blogspot-users/"}}},"pathContext":{"slug":"/2004-01-30-attention-blogspot-users/","prev":{"fields":{"title":"2020 Hindsight: Pixar dumps Disney","date":"30 Jan 2004","slug":"/2004-01-30-2020-hindsight-pixar-dumps-disney/"}},"next":{"fields":{"title":"Getting started with Window","date":"30 Jan 2004","slug":"/2004-01-30-getting-started-with-window/"}}}}

